.buttom_form {
	a {
		display: block;
		width: 285px;
		height: 50px;
		border-radius: 25px;
		background-color: $gold;
		text-align: center;
		// padding-top: 10px
		font-size: 18px;
		text-decoration: none;
		margin-top: 50px;
		color: #000000;
		&:hover {
			text-decoration: underline; }
		&::before {
			display: inline-block;
			content: url("../img/arrow.png");
			vertical-align: middle;
			padding-top: 13px;
			padding-right: 13px;
			margin-left: -30px;
			margin-right: 45px;
			border-right: 1px solid #000000;
			height: 50px; } } }

